<template>
    <div class="view-wrapper" :style="{height:pageMixin_browser.clientHeight}">
        <div class="tips_wrapper">
            <div class="tips_wrapper">
                <div class="icon">

                </div>
                <div class="tips">
                    登录状态失效 请重新授权登录
                </div>

                <div class="btn_group">
                    <DP_Button size="small" background-color="#4CAF50" class="btn" @click.native="login">登录</DP_Button>
                </div>
            </div>


        </div>
        <br>
    </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import DP_Button from '@/components/DP_Button';
    import {wechat_login} from "@/utils/function";

    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content:  this.$route.meta.title+",熊猫博士"
                }],
            }
        },
        name: "error",
        components: {
            DP_Button,
        },
        props: {},
        mixins: [pageMixin],
        data() {
            return {
                tips: ''
            }
        },
        watch: {},
        computed: {},
        methods: {
            login() {
                if (this.$store.getters.getToken) {
                    this.$toast("你已经是登录状态了");
                    return;
                }
                wechat_login();
            }
        },
        created() {
            this.pageMixin_setRequestCompleted(true);
        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
    .view-wrapper {
        .tips_wrapper {
            font-size: 50px;
            width: 750px;
            margin: 0 auto;
            text-align: center;
            position: absolute;
            top: 35%;
            left: 50%;
            transform: translate(-50%, -50%);

            .icon {
                background-image: url("../../assets/global/material/panda.png");
                width: 300px;
                height: 385px;
                background-size: 100% 100%;
                background-size: cover;
                margin: 0 auto;
            }

            .tips {
                font-size: 45px;
                margin: 60px 0;
            }

        }
    }
</style>
